import axios from 'axios';
import {
  PRODUCT_CREATE_FAIL,
  PRODUCT_CREATE_REQUEST,
  PRODUCT_CREATE_RESET,
  PRODUCT_CREATE_SUCCESS,
  PRODUCT_DELETE_FAIL, PRODUCT_DELETE_REQUEST, PRODUCT_DELETE_SUCCESS, PRODUCT_DETAILS_FAIL, PRODUCT_DETAILS_REQUEST, PRODUCT_DETAILS_SUCCESS, PRODUCT_LIST_FAIL, PRODUCT_LIST_REQUEST, PRODUCT_LIST_SUCCESS,
  PRODUCT_REVIEW_CREATE_FAIL,
  PRODUCT_REVIEW_CREATE_REQUEST,
  PRODUCT_REVIEW_CREATE_SUCCESS,
  PRODUCT_TOP_FAIL,
  PRODUCT_TOP_REQUEST,
  PRODUCT_TOP_SUCCESS,
  PRODUCT_UPDATE_FAIL, PRODUCT_UPDATE_REQUEST, PRODUCT_UPDATE_RESET, PRODUCT_UPDATE_SUCCESS
} from './../constants/productConstants';

export const listProducts = (keyword = '', page = 1, size = 4) => async (dispatch) => {
    try {
        
        dispatch({ type: PRODUCT_LIST_REQUEST });

      const { data } = await axios.get(
        `/api/products/`,
        {params: { keyword: keyword, page: page, size: size }}
      );

        dispatch({ type: PRODUCT_LIST_SUCCESS, payload: data });

    } catch (error) {
        dispatch({
            type: PRODUCT_LIST_FAIL,
            payload: error.response && error.response.data.detail ? error.response.data.detail
                : error.message,
        });
        
    }
};

export const getProduct = (id) => async (dispatch) => {
    try {
        
        dispatch({ type: PRODUCT_DETAILS_REQUEST });

        const { data } = await axios.get(`/api/products/${id}`);

        dispatch({ type: PRODUCT_DETAILS_SUCCESS, payload: data });

    } catch (error) {
        dispatch({
            type: PRODUCT_DETAILS_FAIL,
            payload: error.response && error.response.data.detail ? error.response.data.detail
                : error.message,
        });
        
    }
};


export const updateProduct = (product) => async (dispatch, getState) => {
  
    try {
      
      dispatch({ type: PRODUCT_UPDATE_REQUEST });
  
      const {
        userLogin: { userInfo },
      } = getState();
  
  
      const config = {
        headers: {
          'Content-type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        }
      }
  
      const { data } = await axios.put(
        `/api/products/update/${product._id}/`,
        product,
        config
      )
  
  
  
      dispatch({
          type: PRODUCT_UPDATE_SUCCESS,
          payload: data,
      });
        
      dispatch({ type: PRODUCT_UPDATE_RESET });
  
  
    } catch (error) {
      dispatch({
        type: PRODUCT_UPDATE_FAIL,
        payload: error.response && error.response.data.detail ? error.response.data.detail
          : error.message,
      });
    }
  
};
  


export const deleteProduct = (id) => async (dispatch, getState) => {
  
  try {
    
    dispatch({ type: PRODUCT_DELETE_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();


    const config = {
      headers: {
        'Content-type': 'application/json',
        Authorization: `Bearer ${userInfo.token}`,
      }
    }

    const { data } = await axios.delete(
      `/api/products/delete/${id}/`,
      config
    )



    dispatch({
        type: PRODUCT_DELETE_SUCCESS,
        payload: data,
    });
      


  } catch (error) {
    dispatch({
      type: PRODUCT_DELETE_FAIL,
      payload: error.response && error.response.data.detail ? error.response.data.detail
        : error.message,
    });
  }

};




export const createProduct = (product) => async (dispatch, getState) => {
  
  try {
    
    dispatch({ type: PRODUCT_CREATE_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        'Content-type': 'application/json',
        Authorization: `Bearer ${userInfo.token}`,
      }
    }

    const { data } = await axios.post(
      `/api/products/create/`,
      product,
      config
    )

    dispatch({
        type: PRODUCT_CREATE_SUCCESS,
        payload: data,
    });

    dispatch({
      type: PRODUCT_CREATE_RESET,
      payload: data,
    });

  } catch (error) {
    dispatch({
      type: PRODUCT_CREATE_FAIL,
      payload: error.response && error.response.data.detail ? error.response.data.detail
        : error.message,
    });
  }

};


export const createProductReview = (id, review) => async (dispatch, getState) => {
  
  try {
    
    dispatch({ type: PRODUCT_REVIEW_CREATE_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        'Content-type': 'application/json',
        Authorization: `Bearer ${userInfo.token}`,
      }
    }

    const { data } = await axios.post(
      `/api/products/reviews/${id}/`,
      review,
      config
    )

    dispatch({
        type: PRODUCT_REVIEW_CREATE_SUCCESS,
        payload: data,
    });

  } catch (error) {
    dispatch({
      type: PRODUCT_REVIEW_CREATE_FAIL,
      payload: error.response && error.response.data.detail ? error.response.data.detail
        : error.message,
    });
  }

};





export const topProducts = () => async (dispatch) => {
  
  try {
    
    dispatch({ type: PRODUCT_TOP_REQUEST });

    const { data } = await axios.get(`/api/products/top/`)

    dispatch({
        type: PRODUCT_TOP_SUCCESS,
        payload: data,
    });

  } catch (error) {
    dispatch({
      type: PRODUCT_TOP_FAIL,
      payload: error.response && error.response.data.detail ? error.response.data.detail
        : error.message,
    });
  }

};

















